import { Component, OnInit } from '@angular/core';
import { FilterFuctionService } from 'src/app/services/filter-fuction.service';
import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { Uniform_master, Uniform_masterx } from 'src/app/sdk/hr/models';
import { GaUniformMasterService, GaUniformMasterxService } from 'src/app/sdk/hr/services';





@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private gaUniformMasterxService: GaUniformMasterxService,
        private filterFuctionService: FilterFuctionService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService,
    ) {}

    ngOnInit() {
        this.getData()
    }

    listOfDisplayData: Uniform_masterx[] = []
    listOfData: Uniform_masterx[] = []
    getData() {
        this.spinner.show()
        this.gaUniformMasterxService.getGaUniformMasterxes(JSON.stringify({sortKey : this.sortKey, sortValue: this.sortValue, search: this.search})).subscribe(data => {
            this.listOfDisplayData = data.data
            this.listOfData = data.data
            this.spinner.hide()
        })
    }


    search: string = ''
    sortKey: string | null = null
    sortValue: string | null = null
    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key
        this.sortValue = sort.value
        this.getData()
    }


    delete(id) {
        this.spinner.show()
        this.gaUniformMasterxService.deleteGaUniformMasterxesId(id).subscribe(data => {
            this.getData()
            this.spinner.hide()
        })
    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }

}
